.main-container {
  display: flex;
  flex-direction: row-reverse;
  margin-top: 40px;
  margin-bottom: 40px;
  box-shadow: 6px 6px 20px rgba(0, 0, 0, 0.3);
}

/* Main content section */

.main-content-section {
  padding: 25px 50px;
}

.main-content-title {
  font-weight: 700;
  font-size: 26px;
  line-height: 27px;
  color: #000;
  margin-bottom: 10px;
}


/* About me section */
.about-me-section {
  margin: 0 0 40px 0;
}

.about-me-profession {
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 17px;
  color: #000;
  margin: 0 0 25px 0;
}

.about-me-name {
  font-weight: 700;
  font-size: 40px;
  line-height: 1.2;
  color: #000000;
  margin: 0 0 30px 0;
  text-transform: uppercase;

  span {
    text-transform: lowercase;
  }
}

.about-me-description-work {
  margin-bottom: 15px;
}

.about-me-description {
  margin: 0;
  font-size: 14px;
  width: 100%;
  max-width: 600px;
  line-height: 1.3;
  color: var(--color-text);
}




/* My projects section */

.my-projects-section {
  margin: 0 0 50px 0;
}

.my-project-list {
  padding: 0 0 0 18px;
}

.my-projects-item-one {
  color: #000000;
  font-weight: 700;
}

.my-projects-item {
  color: #000000;
  font-weight: 700;
}

.my-projects-text {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 24px;
  color: #595959;
  margin-left: 5px;
}

.my-projects-link {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: #595959;
}

.my-projects-bracket {
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 24px;
  color: #000000;
}

.my-project-info {
  font-size: 10px;
  color: var(--color-text);
}

/* My work expirience */

.my-work-experience-company {
  color: #3fc700;
}

.my-work-experience-company-one {
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  color: #FB6D3A;
  margin: 0 0 10px 0;
}

.my-work-experience-position {
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  color: #000000;
  margin: 0 0 10px 0;
}

.my-work-experience-period {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
  color: #A8A8A8;
  margin: 0 0 10px 0;
}

.my-work-experience-divider {
  color: #000;
}

.my-work-experience-duties {
  padding: 0 0 0 18px;
}

.my-work-experience-duty {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: #595959;
}

.my-work-experience-company-block {
  margin: 0 15px 0 0;
}

.my-work-experience-period-company-one {
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 15px;
  color: #A8A8A8;
  margin: 10px 0 10px 0;
}


/* My education section */

.my-education-section {
  margin: 45px 0 0 0;
}

.my-education-name-title {
  margin: 0;
}

.my-education-name {
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  color: #FB6D3A;
}

.my-education-management {
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  color: #000000;
  margin: 10px 0 0 0;
}

/* Sidebar-section */

.sidebar-section {
  background: linear-gradient(135deg, #2F005F 0%, #000511 100%);
  width: 100%;
  max-width: 390px;
  padding: 40px 0;
}

.contacts-section,
.tech-skills-section {
  margin: 50px 0 0 40px;
}

.sidebar-title {
  font-style: normal;
  font-weight: 700;
  font-size: 30px;
  line-height: 27px;
  color: #FFFFFF;
  margin-bottom: 10px;
}

.contacts-telegram {
  margin-bottom: 15px;
}

/* Contacts section */

.contacts-section-type {
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 24px;
  color: #FFFFFF;
  margin-right: 7px;
}

.contacts-section-link {
  color: #fb6d3a;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
}


/* Tech skills list */

.tech-skills-list {
  padding: 0 0 0 18px;
}

.tech-skills-item::marker {
  color: #FB6D3A;
}

.tech-skills-item {
  color: #FB6D3A;
}

.tech-skills-item-text {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: #FFFFFF;
}


/* Soft skills section */

.soft-skills-section {
  margin: 50px 0 0 40px;
}

.soft-skills-section-header {
  margin-bottom: 10px;
  font-style: normal;
  font-weight: 700;
  font-size: 22px;
  line-height: 27px;
  color: #FFFFFF;
}

.soft-skills-list {
  padding: 0 0 0 18px;
  color: #FB6D3A;
}

.soft-skills-item-text {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: #FFFFFF;
}
